body {
  font-family: Arial, sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  background-color: #f4f4f4;
}

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

input[type="number"] {
  margin-bottom: 20px;
  width: 80px;
  text-align: center;
  font-size: 1.2em;
}

input[type="range"] {
  width: 300px;
  margin-bottom: 20px;
}

#radioButtons {
  display: flex;
  gap: 10px;
}

label {
  font-size: 1.1em;
}
